export class BOOSTLET {
  constructor() {
    this.version = '1.0.0';
    this.categories = {};
    this._currentCategory = null;
  }

  // Getter for category
  get category() {
    return this._currentCategory;
  }

  // Setter for category
  set category(categoryName) {
    if (!this.categories.hasOwnProperty(categoryName)) {
      this.categories[categoryName] = [];
    }
    this._currentCategory = categoryName;
  }
}

window.BOOSTLET = new BOOSTLET();


